import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { staticData } from "../config/db.constant";

export const DB_Data = staticData;
@Injectable({
    providedIn: 'root',
  })
export class StaticDataService{
    public prefixData() : Observable<string[]> {
          return of(DB_Data.prefix);
    }
    public genderData() : Observable<string[]>{
        return of(DB_Data.gender)
    }
    public maritalStatusData() : Observable<string[]>{
        return of(DB_Data.maritalStatus)
    }
    public nationalityData() : Observable<string[]>{
        return of(DB_Data.nationality)
    }
    public residenceData() : Observable<string[]>{
        return of(DB_Data.residenceType)
    }
    public documentType() : Observable<string[]>{
        return of(DB_Data.documentType)
    }
    public relationshipType() : Observable<string[]>{
        return of(DB_Data.relationshipType)
    }
}

