import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoadingController, ModalController } from '@ionic/angular';
import { BranchService } from 'src/app/pages/v2/kyc-branch/kyc-branch.service';
import { RekycPopupComponent } from '../rekyc-popup/rekyc-popup.component';
import { ApiService } from 'src/app/services/api.service';
import { ApiServiceV2 } from 'src/app/services/api-v2.service';

@Component({
  selector: 'app-duplication',
  templateUrl: './duplication.component.html',
  styleUrls: ['./duplication.component.scss'],
})
export class DuplicationComponent implements OnInit {
  firstScreen: boolean = true;
  duplicationForm: FormGroup;
  action: string;
  @Input() customerData: any;
  relationshipArr: string[] = [];
  constructor(
    private modalCtrl: ModalController,
    private fb: FormBuilder,
    private branchService: BranchService,
    private apiService: ApiServiceV2,
    private loadingCtrl: LoadingController
  ) {}

  ngOnInit() {
    this.buildDuplicationForm();
    console.log(this.customerData);
    this.getRelationshipTypeArr();
  }

  buildDuplicationForm() {
    this.duplicationForm = this.fb.group({
      phoneNumberChecked: [false],
      emailChecked: [false],
      relationship: ['', Validators.required],
      remark: ['', [Validators.required]],
    });
  }
  closeModal(message: string) {
    this.modalCtrl.dismiss(message);
  }
  onNext(isReKyc) {
    this.firstScreen = false;
    this.customerData.reKyc = isReKyc;
  }
  onBack() {
    this.firstScreen = true;
  }
  async onSubmit() {
    //payload for accept Re-kyc api
    var payload = {
      customerId: this.customerData[0]?.customerId,
      phoneNumber: this.customerData[0]?.phoneNumber,
      primaryEmailAdress: this.customerData[0]?.primaryEmailAdress,
      address: this.customerData[0]?.address,
      relationship: this.duplicationForm?.value?.relationshipType,
      remarks: this.duplicationForm?.value?.remark,
      isReKyc: this.customerData[0]?.reKyc,
      phoneNumberPresent: this.customerData[0]?.phoneNumberPresent,
      emailPresent: this.customerData[0]?.emailPresent,
    };
    const loading = await this.loadingCtrl.create({
      message: 'Please Wait...',
      translucent: true,
    });
    await loading.present();
    this.branchService.updateKyc(payload).subscribe(
      (resp) => {
        if (resp.statusCode == 200) {
          if (this.customerData[0]?.reKyc == true) {
            loading.dismiss();
            this.closeModal(this.customerData);
          } else {
            loading.dismiss();
            this.closeModal(this.customerData);
          }
        }
      },
      (err) => loading.dismiss()
    );
  }
  async openDialog() {
    let modal = await this.modalCtrl.create({
      component: RekycPopupComponent,
      backdropDismiss: true,
      cssClass: 'dup-modal',
      componentProps: {
        data: '',
      },
    });
    return await modal.present();
  }

  /**
   * get relationshipType dropdown values
   * @param key key for the api
   */
  getRelationshipTypeArr() {
    this.apiService.relationshipType().subscribe((data) => {
      this.relationshipArr = data;
    });
  }
}
