import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-add-account',
  templateUrl: './add-account.component.html',
  styleUrls: ['./add-account.component.scss'],
})
export class AddAccountComponent implements OnInit {
  account_option: string[] = ['Internal', 'Other Bank'];
  selectedItem: string = "Internal";
  accountForm: FormGroup;
  isSubmitted = false;
  currentUser: any;

  constructor(private modalCtr: ModalController,
    public formBuilder: FormBuilder,
    private apiService: ApiService,
    private shareDataService: DataService,
    public toastService: ToastService) { }

  ngOnInit() {
    this.currentUser = this.shareDataService.getCurrentUser();
    this.buildForm();

    this.accountForm.get('transferType')?.valueChanges.subscribe(val => {
      if (val === 'Internal') {
        this.accountForm.get('bankCode')?.patchValue("");
        this.accountForm.get('bankCode').clearValidators(); // 6. Clear All Validators
        this.accountForm.get('bankCode').updateValueAndValidity();
      } else {
        this.accountForm.get('bankCode').setValidators([Validators.required]); // Set Required Validator
        this.accountForm.get('bankCode').updateValueAndValidity();
      }
    })
  }

  buildForm() {
    this.accountForm = this.formBuilder.group({
      customerId: [this.currentUser?.customerId, [Validators.required]],
      transferType: [this.selectedItem, [Validators.required]],
      accountCurrency: [this.currentUser?.currencyCode ?? 'INR', [Validators.required]],
      accountNumber: ['', [Validators.required]],
      customerName: ['', [Validators.required]],
      bankCode: [''],
      customerNickName: ['', [Validators.required]]
    });
  }

  get errorControl() {
    return this.accountForm.controls;
  }

  submit() {
    this.isSubmitted = true;
    if (!this.accountForm.valid) {
      return false;
    } else {
      console.log(this.accountForm.value);
      this.apiService.addNewAccountToDisburse(this.accountForm.value).subscribe(res => {
        console.log(res);
        if (res.status == 200) {
          this.close();
          this.toastService.showToast(res?.message);
        } else {
          this.toastService.showToast(res?.message);
        }
      }, (err: any) => {
        this.toastService.showToast(err);
      });
    }
  }

  close() {
    this.modalCtr.dismiss(null);
  }

}
