import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  OnInit,
  Output,
} from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { Subscription } from 'rxjs';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import { ScanDocComponent } from '../../scan-doc/scan-doc.component';
import { FileUploadService } from '../file-upload.service';
import { AadharModel } from '../data.modal';
import { CropComponent } from '../../crop/crop.component';
import { StaticDataService } from 'src/app/services/static-data-service';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-customer-manual-verification',
  templateUrl: './customer-manual-verification.component.html',
  styleUrls: [
    './customer-manual-verification.component.scss',
    '../verification-modal.component.scss',
  ],
})
export class CustomerManualVerificationComponent implements OnInit {
  @Output() isFormValid = new EventEmitter<boolean>();
  cropImage: any;
  isFrontDocUploaded: boolean = false;
  isBackDocUploaded: boolean = false;

  scanImage: any;
  manualVerificationForm: FormGroup;
  selectedIndex: number;
  constructor(
    private api: ApiService,
    private cdr: ChangeDetectorRef,
    private modalController: ModalController,
    private fileService: FileUploadService,
    private staticDataService: StaticDataService,
    private fb: FormBuilder
  ) {}
  ngOnInit(): void {
    this.getDocumentName();
    this.initManualForm();
    this.documentItems.items = [];
  }
  frontFile: any;
  isupload: boolean;
  isAuto_rekyc: boolean = true;
  isscan: boolean;
  isrekyc: boolean;
  isedit: boolean;
  isFile: boolean = false;
  filename: string;
  base64File: any;
  files: any = [];
  percentDone: number;
  uploadSuccess: boolean;
  isUploading: boolean = false;
  uploadingFile: string;
  requestSubscription: Subscription;
  parsedText: any;
  parsertext: string;
  otpValue: any;
  documentName: any;
  documentNumber: any;
  documentName_rekyc: any;
  documentNumber_rekyc: any;
  customerId: any;
  DocumentId: any;
  customerData: any;
  isUploaded: boolean = false;
  isScanned: boolean = false;
  back: boolean = false;
  front: boolean = false;
  @Output() emitAadharData = new EventEmitter();

  docArr: any[] = [];

  fileNameCheckBack: any;
  urlB: any;
  urlback: any;
  aadharData: AadharModel;
  backImg: any;
  frontImg: any;
  disableSubmit: boolean = true;
  UploadData: any[] = ['Front Uploaded', 'Back Uploaded'];
  documentItems: any = {};

  onFileSelect(e: any, index: number): void {
    try {
      this.selectedIndex = index;
      const file = e.target.files[0];
      const fReader = new FileReader();
      fReader.readAsDataURL(file);
      fReader.onloadend = async (_event: any) => {
        this.filename = file.name;
        this.base64File = _event.target.result;
        this.openDocuemntCropModal(file);
        await this.fetchDocumentDetails(file,index);
        this.documents
          .at(this.selectedIndex)
          .get('fileUrl')
          .setValue(fReader.result);
        this.documents
          .at(this.selectedIndex)
          .get('fileName')
          .setValue(file.name);
        this.fileUpload(file);
      };
    } catch (error) {
      this.filename = null;
      this.base64File = null;
    }
  }

  async fetchDocumentDetails(file,index) {
    let side = this.documents.at(this.selectedIndex).get('documentTitle').value;
    this.fileService
      .readAadharData(file, side.toLowerCase())
      .subscribe((res) => {
        if (res) {
          if (side == 'Front') {
            this.fetchFrontData(file, res, index);
          } else if (side == 'Back') {
            this.fetchBackData(file, res, index);
          }
        }
      });
  }
  fetchFrontData(file, res, index) {
    let name = res?.data?.name.value.split(' ');
    let data = {
      firstName: name[0],
      lastName: name[name.length - 1],
      dateOfBirth: res?.data?.dob.value,
      gender:
        res?.data?.gender.value.charAt(0).toUpperCase() +
        res?.data?.gender.value.slice(1),
      documentNumber: res?.data?.no.value,
    };
    this.aadharData = { ...this.aadharData, ...data };
    this.emitAadharData.emit(this.aadharData);
    this.setPayloadModal(file,index);
  }
  fetchBackData(file, res, index) {
    let dataBack = {
      userAddress: [
        {
          address1: res?.data?.address.value,
          address2: '',
          zipCode: res?.data?.pin.value,
          country: '',
          state: res?.data?.state.value,
          city: '',
        },
      ],
    };
    this.aadharData = { ...this.aadharData, ...dataBack };
    this.emitAadharData.emit(this.aadharData);
    this.setPayloadModal(file,index);
  }
  setPayloadModal(file,index) {
    let docBack = {
      documentName: this.manualVerificationForm.get('docName').value,
      documentNumber: this.aadharData.documentNumber,
      documentSide: index + 1,
      documentType: this.getDocType(
        this.manualVerificationForm.get('docName').value
      ),
      fileName: file.name,
      fileType: file.type,
      fileUrl: this.documents.controls[this.selectedIndex].get('fileUrl').value,
    };
    this.documentItems.items.push(docBack);
    localStorage.setItem(
      'UploadedDocument',
      JSON.stringify(this.documentItems)
    );
    this.isFormValid.emit(this.manualVerificationForm.valid);
  }

  getDocType(docName: string) {
    let docType;
    if (docName === 'Aadhar card') {
      docType = '6';
    } else if (docName === 'Pan card') {
      docType = '7';
    } else if (docName === 'Passport') {
      docType = '8';
    }
    return docType;
  }

  fileUpload(file) {
    this.isUploading = true;
    this.requestSubscription = this.api
      .uploadAndProgress(file)
      .subscribe((event: any) => {
        if (event.type === HttpEventType.UploadProgress) {
          this.percentDone = Math.round((100 * event.loaded) / event.total);
          this.isUploaded = true;
        } else if (event instanceof HttpResponse) {
          this.percentDone = 0;
          this.isUploading = false;
          this.cdr.markForCheck();
        }
      });
  }

  cancel(index: number) {
    this.requestSubscription.unsubscribe();
    this.documents.controls[index].get('fileUrl').setValue('');
    this.cdr.markForCheck();
  }

  DataURIToBlob(dataURI: string) {
    const splitDataURI = dataURI.split(',');

    const byteString =
      splitDataURI[0].indexOf('base64') >= 0
        ? atob(splitDataURI[1])
        : decodeURI(splitDataURI[1]);

    const mimeString = splitDataURI[0].split(':')[1].split(';')[0];

    const ia = new Uint8Array(byteString.length);

    for (let i = 0; i < byteString.length; i++)
      ia[i] = byteString.charCodeAt(i);

    return new Blob([ia], { type: mimeString });
  }
  async openDocScan(index: number) {
    this.selectedIndex = index;
    let modal = await this.modalController.create({
      component: ScanDocComponent,
      backdropDismiss: true,
    });
    modal.onDidDismiss().then((res) => {
      let scannedImage = res?.data?.resImage;
      this.fetchDocumentDetails(this.DataURIToBlob(scannedImage),index);
      this.documents
        .at(this.selectedIndex)
        .get('fileUrl')
        .setValue(scannedImage);
      let fileName =
        this.manualVerificationForm.get('docName').value +
        ' ' +
        this.documents.at(this.selectedIndex).get('documentTitle').value;
      this.documents.at(this.selectedIndex).get('fileName').setValue(fileName);
      this.isFormValid.emit(this.manualVerificationForm.valid);
    });

    return await modal.present();
  }

  async openDocuemntCropModal(file) {
    let modal = await this.modalController.create({
      component: CropComponent,
      componentProps: {
        file: file,
      },
    });
    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      let fr = new FileReader();
      fr.readAsDataURL(res?.data);
      fr.onload = (_event) => {
        this.cropImage = this.cropImage = fr.result;
      };
    });

    return await modal.present();
  }

  /** get document type dropdown values */
  getDocumentName() {
    this.staticDataService.documentType().subscribe((res) => {
      this.docArr = res;
      console.log(res);
    });
  }

  /** Init manualverification form and update validation while changing form value */
  initManualForm() {
    this.manualVerificationForm = this.fb.group({
      docName: ['', [Validators.required]],
      isScan: [false],
      isUpload: [false],
      documents: this.fb.array([this.addNewPage(0), this.addNewPage(1)]),
    });
  }

  /** get the documnt form array controls in manual verification form */
  get documents(): FormArray {
    return this.manualVerificationForm.get('documents') as FormArray;
  }

  /**
   * form group for docuemnt upload
   * @param side of the docuemnt
   * @returns the form group each side document
   */
  addNewPage(side: number): FormGroup {
    return this.fb.group({
      id: [],
      fileUrl: ['', Validators.required],
      fileName: ['', Validators.required],
      documentSide: [side],
      documentTitle: [this.getPageName(side), Validators.required],
    });
  }

  /**
   * document side name defined
   * @param side of the docuemnt
   * @returns side name for the docuement
   */
  getPageName(side: number) {
    switch (side) {
      case 0:
        return 'Front';
      case 1:
        return 'Back';
      default:
        return 'Other';
    }
  }

  /**
   * scan upload toggle changes
   * @param event toggle event
   * @returns while no document selected
   */
  onToggle(event) {
    if (this.manualVerificationForm.controls.docName.invalid) {
      return;
    }
    this.manualVerificationForm.get('isScan').setValue(event);
    this.manualVerificationForm.get('isUpload').setValue(!event);
    this.documents.controls.map((doc) => {
      doc.get('fileUrl').setValue('');
      doc.get('fileName').setValue('');
    });
  }

  /**
   * delete the document
   * @param index index of the document to be deleted
   */
  deleteDoc(index) {
    this.documents.controls[index].get('fileUrl').setValue('');
  }
}
