import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { WebcamImage, WebcamInitError } from 'ngx-webcam';
import { Observable, Subject } from 'rxjs';
import {
  CameraPreview,
  CameraPreviewOptions,
} from '@ionic-native/camera-preview/ngx';

@Component({
  selector: 'app-capture-image',
  templateUrl: './capture-image.component.html',
  styleUrls: ['./capture-image.component.scss'],
})
export class CaptureImageComponent implements OnInit {
  @Input() screenData: any;
  [x: string]: any;
  private trigger: Subject<any> = new Subject();
  public webcamImage!: WebcamImage;
  private nextWebcam: Subject<any> = new Subject();
  sysImage = '';
  img = '';
  error?: string;
  capturedImage;
  isVisible = true
  isDone: boolean = false;
  constructor(
    private modalController: ModalController,
    private preview: CameraPreview
  ) {}

  ngOnInit() {
    this.startPreview();
  }

  public captureImg(webcamImage: WebcamImage): void {
    this.webcamImage = webcamImage;
    this.sysImage = webcamImage!.imageAsDataUrl;
    console.info('got webcam image', this.sysImage);
    this.isDone = true;
  }
  public get invokeObservable(): Observable<any> {
    return this.trigger.asObservable();
  }
  public get nextWebcamObservable(): Observable<any> {
    return this.nextWebcam.asObservable();
  }
  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }

  public getSnapshot(): void {
    this.trigger.next();
  }

  back() {
    this.preview.stopCamera()
    this.modalController.dismiss();
  }
  closeModal() {
    let data = this.img;
    this.modalController.dismiss(data);
  }

  startPreview() {
    if (window.confirm('Do you want to start camera?')) {
      const cameraPreviewOpts: CameraPreviewOptions = {
        x: 5,
        y: 210,
        width: window.screen.width,
        height: window.screen.height / 3,
        camera: 'front',
        alpha: 1,
      };
      this.preview.startCamera(cameraPreviewOpts).then(
        (res) => {},
        (err) => {
          alert(JSON.stringify(err));
        }
      );
    }
  }

  capturePreview() {
    // if (window.confirm('Do you want to capture image?')) {
      const pictureOpts: any = {
        quality: 100,
      };

      // take a picture
      this.preview.takePicture(pictureOpts).then((imageData) => {
        this.isVisible = false;
        this.img = 'data:image/jpg;base64,' + imageData;
        this.preview.stopCamera();
      });
    }
}
