import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { BranchService } from 'src/app/pages/v2/kyc-branch/kyc-branch.service';

@Component({
  selector: 'app-otp-verification-popup',
  templateUrl: './otp-verification-popup.component.html',
  styleUrls: ['./otp-verification-popup.component.scss'],
})
export class OtpVerificationPopup implements OnInit {
  otpForm: FormGroup;
  @Input() Mnumber: string;
  @Input() source_key:string
  config = {
    allowNumbersOnly: true,
    length: 6,
    isPasswordInput: true,
    disableAutoFocus: false,
    placeholder: '',
    inputStyles: {
      width: '60px',
      height: '60px',
    },
  };
  show: number;
  otp: any;
  disableProceed: boolean = true;
  isLoading:boolean = false;
  constructor(
    private fb: FormBuilder,
    private modalController: ModalController,
    private branchService: BranchService
  ) {}

  ngOnInit() {
    this.otpForm = this.fb.group({
      phoneNo: [''],
      otp: ['', [Validators.required]],
    });
  }

  back() {
    this.modalController.dismiss();
  }

  resendOtp() {
    let payload = {
      source: 'customer',
      source_key: 'mobile',
      source_value: this.Mnumber,
    };
    this.branchService.getOtp(payload).subscribe((res) => {
      console.log(res);
    });
  }
  onOtpChange(otp) {
    console.log(otp);

    this.otp = otp;
   if(otp.length == 6){
    this.disableProceed = false;
   }
   else{
    this.disableProceed = true;
   }
  }

  verifyOtp() {
    let payload = {
      sourceKey: this.source_key,
      sourceValue: this.Mnumber,
      otp: this.otp
    };
    this.isLoading = true;
    this.branchService.verifyOtp(payload).subscribe((res) => {
      if(res){
        this.isLoading = false;
        this.modalController.dismiss({otp:this.otp});
      }
      else{
        this.isLoading = false
      }
    },err => this.isLoading = false);
  }
}
