import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';

import { ModalController } from '@ionic/angular';
import { DuplicationComponent } from '../duplication/duplication.component';

import { AadharModel } from './data.modal';
import { ApiServiceV2 } from 'src/app/services/api-v2.service';

@Component({
  selector: 'app-verification-modal',
  templateUrl: './verification-modal.component.html',
  styleUrls: ['./verification-modal.component.scss'],
})
export class VerificationModalComponent implements OnInit {
  @Input() data: any;
  @Input() screen: string;
  @Input() isAutoSelected: boolean;

  isupload: boolean;
  isscan: boolean;
  isrekyc: boolean;
  isedit: boolean;
  isFile: boolean = false;
  filename: string;

  files: any = [];

  otpValue: any;
  documentName: any;
  documentNumber: any;

  customerId: any;
  DocumentId: any;
  customerData: any;

  selectedFiles: any;
  fileNameCheckBack: any;
  urlB: any;
  urlback: any;
  aadharData: AadharModel;
  backImg: any;
  frontImg: any;
  disableSubmit: boolean = true;
  isUploaded: any;
  autoSubmiut: any;
  isFormValid: boolean = false;

  constructor(
    private router: Router,
    private api: ApiService,
    private apiV2: ApiServiceV2,
    // private cdr: ChangeDetectorRef,
    private modalController: ModalController
  ) // private snack: MatSnackBar,
  // private http:HttpClient,
  // private fileService:FileUploadService,
  {}
  customerArr: any[] = [
    {
      id: 1,
      checked: true,
      name: 'New Customer',
    },

    {
      id: 2,
      checked: false,
      name: 'Existing Customer',
    },
  ];
  toggleArr: any[] = [
    {
      id: 1,

      checked: true,

      name: 'Auto',
    },

    {
      id: 2,

      checked: false,

      name: 'Manual',
    },
  ];
  docArr: any[] = ['Aadhar Card', 'Pan Card', 'Passport'];
  options: any[] = [
    {
      id: 1,
      checked: false,
      name: 'OTP',
    },
    {
      id: 2,
      checked: false,
      name: 'Biometric',
    },
  ];
  isExistingCustomer: boolean = false;
  isAuto: boolean = true;
  isOtp: boolean;
  isBiometric: boolean;

  ngOnInit() {
    // this.isAuto ??= this.isAutoSelected
    // if(this.isAutoSelected == false){
    //   this.toggleArr.map(toggle =>{
    //     toggle.checked = !toggle.checked
    //   })
    // }
    console.log(this.isAutoSelected);

    console.log(this.screen);
  }
  selectedTab1: string = 'new_customer';
  CustomerType(value) {
    // this.isExistingCustomer = event;
    this.isAuto = true;
    this.customerId = '';

    if (value == 'Existing Customer') {
      this.isExistingCustomer = true;
    } else {
      this.isExistingCustomer = false;
    }
    this.documentName = '';
    this.documentNumber = '';
    this.otpValue = '';
    this.isOtp = false;
    this.isBiometric = false;
    this.isupload = false;
    this.isscan = false;
    this.isrekyc = false;
    this.isedit = false;
  }
  autoManualToggle(value) {
    if (value == 'Auto') {
      this.isAuto = true;
      this.isUploaded = false;
    } else {
      this.isAuto = false;
      this.isUploaded = false;
    }
  }

  verificationMethodToggle(evt) {
    console.log(evt);
    if (!this.documentName || !this.documentNumber) {
      return;
    }
    // if ((!this.documentName_rekyc || !this.documentNumber_rekyc)) {
    //   return;
    // }

    if (evt == 'otp') {
      this.isOtp = true;
      this.isBiometric = false;
    } else {
      this.isOtp = false;
      this.isBiometric = true;
    }
  }
  radioButtonChange(evt) {
    console.log(evt);
  }

  Rekyc(value) {
    if (value == 'Re-kyc' && this.customerId) {
      this.isrekyc = true;
      this.isedit = false;
    }
  }
  edit_info(value) {
    if (value == 'edit' && this.customerId) {
      this.isrekyc = false;
      this.isedit = true;
    }
  }

  gotobasicScreen() {
    this.router.navigate['basicscreen'];
  }
  closeModal() {
    if (this.isedit == true) {
      this.modalController.dismiss({ customerData: this.customerData });
      return;
    }
    if (!this.isAuto && !this.isedit) {
      this.modalController.dismiss({ customerData: this.aadharData });
      return;
    }

    this.modalController.dismiss({ customerData: this.aadharData });
  }
  backModal() {
    this.modalController.dismiss();
  }

  async openDialog() {
    let modal = await this.modalController.create({
      component: DuplicationComponent,
      backdropDismiss: true,
      cssClass: 'dup-modal',
      componentProps: {
        data: '',
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
    });

    return await modal.present();
  }
  getCustomerDetails() {
    this.isUploaded = true;
    this.apiV2.getCustomerInfo(this.customerId).subscribe((res) => {
      console.log(res?.data[0]);
      this.customerData = res?.data[0];
    });
  }

  validateForm(event) {
    this.disableSubmit = !event;
    this.isFormValid = event;
  }
  getAadharData(event) {
    this.aadharData = event;
  }
  updateEnableSubmit(evt) {
    this.isUploaded = evt;
    this.isFormValid = evt;
  }

  updateFormValidation(event) {
    this.isFormValid = event;
  }
}
