import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { DocScannerConfig } from 'ngx-document-scanner';
import { WebcamImage, WebcamInitError } from 'ngx-webcam';
import { Observable, Subject } from 'rxjs';
import { CropComponent } from '../crop/crop.component';
import { HttpEventType, HttpResponse } from '@angular/common/http';
@Component({
  selector: 'app-scan-doc',
  templateUrl: './scan-doc.component.html',
  styleUrls: ['./scan-doc.component.scss'],
})
export class ScanDocComponent implements OnInit {
  image: string | ArrayBuffer;
  selectedImage: string | ArrayBuffer;

  constructor(private modalController: ModalController) {}
  // @Input() screenData:any
  [x: string]: any;
  private trigger: Subject<any> = new Subject();
  public webcamImage!: WebcamImage;
  private nextWebcam: Subject<any> = new Subject();
  sysImage = '';
  error?: string;
  isDone: boolean = false;

  ngOnInit() {}
  config: DocScannerConfig = {
    editorBackgroundColor: '#fafafa',
    buttonThemeColor: 'primary',
    cropToolColor: '#ff4081',
    cropToolShape: 'circle',
    exportImageIcon: 'cloud_download',
  };
  uploadImg(event) {
    let selectedFile = event.target.files[0];
    this.image = selectedFile;
    let fileReader = new FileReader();
    fileReader.readAsDataURL(selectedFile);
    fileReader.onload = (_event) => {
      this.selectedImage = fileReader.result;
    };
  }
  public captureImg(webcamImage: WebcamImage): void {
    this.webcamImage = webcamImage;
    this.sysImage = webcamImage!.imageAsDataUrl;
    console.info('got webcam image', this.sysImage);
    this.isDone = true;
    this.file = this.DataURIToBlob(this.sysImage)
    // this.openDocuemntCropModal(this.DataURIToBlob(this.sysImage));
  }
  public get invokeObservable(): Observable<any> {
    return this.trigger.asObservable();
  }
  public get nextWebcamObservable(): Observable<any> {
    return this.nextWebcam.asObservable();
  }
  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }


  public getSnapshot(): void {
    this.trigger.next();
  }

  back() {
    this.modalController.dismiss();
  }
  closeModal() {
    this.modalController.dismiss();
  }
  retake() {
    this.webcamImage = null;
    this.sysImage = null;
    this.isDone = false;
  }

  async openDocuemntCropModal(file) {
    let modal = await this.modalController.create({
      component: CropComponent,
      componentProps: {
        file: file,
      },
    });
    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      let fr = new FileReader();
      fr.readAsDataURL(res?.data);
      fr.onload = (_event) => {
        this.cropImage = _event.target.result;
        this.fileUpload(this.cropImage)
      };
    });

    return await modal.present();
  }
  onSubmit(){
    this.modalController.dismiss({
      resImage:this.sysImage,
      file:this.file
    });}

    DataURIToBlob(dataURI: string) {
      const splitDataURI = dataURI.split(',');

      const byteString =
        splitDataURI[0].indexOf('base64') >= 0
          ? atob(splitDataURI[1])
          : decodeURI(splitDataURI[1]);

      const mimeString = splitDataURI[0].split(':')[1].split(';')[0];

      const ia = new Uint8Array(byteString.length);

      for (let i = 0; i < byteString.length; i++)
        ia[i] = byteString.charCodeAt(i);

      return new Blob([ia], { type: mimeString });
    }

    editResult(event){
      console.log(event);
    }
    onError(event){
      console.log(event);
    }
    editorState(event){
      console.log(event);
    }
    exitEditor(event){
      console.log(event);    
    }

}



