import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-financial-details',
  templateUrl: './financial-details.component.html',
  styleUrls: ['./financial-details.component.scss','../professional-information/professional-information.component.scss'],
})
export class FinancialDetailsComponent implements OnInit {

  constructor(private fb:FormBuilder) { }

  ngOnInit() {
    this.createForm();
  }
  financialInfoForm:FormGroup;
  incomeTypeArr:String[];
  frequencyArr:String[];
  currencyArr:String[];
  amountArr: any[];
  expenseTypeArr:String[];
  expenseCurArr:String[];
  expAmtArr:String[];




  

  createForm(){
    this.financialInfoForm = this.fb.group({
      incomeDetails: this.fb.array([this.incomeForm()]),
      expenseDetails:this.fb.array([this.expenseForm()]),
    });
    console.log(this.financialInfoForm);
    
  }
  incomeForm(){
    return this.fb.group({
      incomeType: ['', [Validators.required]],
      frequency: ['', [Validators.required]],
      currency: ['', [Validators.required]],
      amount: ['', [Validators.required]],
    })
  }
  expenseForm(){
    return this.fb.group({
      expenseType: ['',[Validators.required]],
      expenseCurrency: ['',[Validators.required]],
      expenseAmount: ['',[Validators.required]],
    })
  }
  get incomeDetails(): FormArray {
    return this.financialInfoForm.get('incomeDetails') as FormArray;
  }
 get expenseDetails():FormArray{
    return this.financialInfoForm.get('expenseDetails') as FormArray;
  }
  addIncomeDetails(){
    this.incomeDetails.push(this.incomeForm());
  }
  addExpenseDetails(){
    this.expenseDetails.push(this.expenseForm());
  }
  removeExpenseDetail(index:number){
    this.expenseDetails.removeAt(index);
  }
  removeIncomeDetail(index:number){
    this.incomeDetails.removeAt(index);
  }

}
