import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { CashDepositV2Page } from 'src/app/pages/v2/cash-deposit-v2/cash-deposit-v2.page';
import { CashWithdrawV2Page } from 'src/app/pages/v2/cash-withdraw-v2/cash-withdraw-v2.page';
import { ForexCashV2Page } from 'src/app/pages/v2/forex-cash-v2/forex-cash-v2.page';
import { LoanDisbursementV2Page } from 'src/app/pages/v2/loan-disbursement-v2/loan-disbursement-v2.page';
import { LoanRepaymentV2Page } from 'src/app/pages/v2/loan-repayment-v2/loan-repayment-v2.page';

@Component({
  selector: 'app-denomination-slide',
  templateUrl: './denomination-slide.component.html',
  styleUrls: ['./denomination-slide.component.scss'],
})
export class DenominationSlideComponent implements OnInit {

  denominationForm: FormGroup;
  denomination_keys: any = [{
    key: "two_thousand",
    value: 2000,
    denom_key: 2000
  },
  {
    key: "five_hundred",
    value: 500,
    denom_key: 500
  }, {
    key: "two_hundred",
    value: 200,
    denom_key: 200
  }, {
    key: "one_hundred",
    value: 100,
    denom_key: 100
  }, {
    key: "fifty",
    value: 50,
    denom_key: 50
  }, {
    key: "twenty",
    value: 20,
    denom_key: 20
  }, {
    key: "ten",
    value: 10,
    denom_key: 10
  }, {
    key: "one",
    value: 1,
    denom_key: 1
  },
  ];

  total: number = 0;
  @Output() amountEvent = new EventEmitter<any>();
  @Input() denomArea: any;
  @Input() isFrmOpened: any;
  @Input() isFtmOpened: boolean;
  @Output() emitTotalValue = new EventEmitter<any>();

  public parentRef: ForexCashV2Page;
  public cashDepositRef: CashDepositV2Page;
  public chwViewRef: CashWithdrawV2Page;
  public loanViewRef: LoanRepaymentV2Page;
  public loanDsbViewRef: LoanDisbursementV2Page;
  public unique_key: string;
  public denomData: any = [];

  showMore: boolean = false;
  icon_arrow_svg: any = 'assets/icon/arrow.svg';

  constructor(private fb: FormBuilder) { }

  ngOnInit() {
    this.buildDenomForm();

    let data = JSON.parse(localStorage.getItem(this.unique_key)) || null;

    if (this.denomData != null) {
      this.populateDenomination();
      this.calculateTotalAmount();
    } else {
      this.denominationForm.patchValue(data?.data);
    }

    this.denominationForm.valueChanges.subscribe(val => {
      let sum: number = 0;
      Object.keys(this.denominationForm.controls).forEach(key => {
        for (let index = 0; index < this.denomination_keys.length; index++) {
          const denomItem = this.denomination_keys[index];
          if (denomItem.key == key) {
            sum += denomItem.value * Number(this.denominationForm.get(`${key}`)?.value);
          }
        }
      });
      this.total = sum;
      this.emitTotalValue.emit(this.total);
      this.saveDenom();
    });
  }

  buildDenomForm() {
    this.denominationForm = this.fb.group({
      two_thousand: '',
      five_hundred: '',
      two_hundred: '',
      one_hundred: '',
      fifty: '',
      twenty: '',
      ten: '',
      one: '',
    });
  }

  calculateTotalAmount() {
    let sum = 0;
    Object.keys(this.denominationForm.controls).forEach(key => {
      for (let index = 0; index < this.denomination_keys.length; index++) {
        const denomItem = this.denomination_keys[index];
        if (denomItem.key == key) {
          sum += denomItem.value * Number(this.denominationForm.get(`${key}`)?.value);
        }
      }
    });

    this.total = sum;
  }

  remove_me() {
    if (this.unique_key == 'viewRef' || this.unique_key == 'dynamicViewRef') {
      this.parentRef.remove(this.unique_key);
    } else if (this.unique_key == 'cashViewRef') {
      this.cashDepositRef.remove(this.unique_key);
    } else if (this.unique_key == 'chwViewRef') {
      this.chwViewRef.remove(this.unique_key);
    } else if (this.unique_key == 'loanViewRef') {
      this.loanViewRef.remove(this.unique_key);
    } else if (this.unique_key == 'loanDsbViewRef') {
      this.loanDsbViewRef.remove(this.unique_key);
    }
  }

  populateDenomination() {
    this.denomination_keys.forEach((key, i) => {
      for (let index = 0; index < this.denomData.length; index++) {
        const denomItem = this.denomData[index];
        if (denomItem.denominationKey === key.denom_key) {
          this.denominationForm.get(key?.key)?.patchValue(denomItem?.denominationValue);
        }
      }
    });
  }

  saveDenom() {
    let final = [
      {
        denominationKey: 2000,
        denominationValue: +this.denominationForm.get('two_thousand')?.value
      },
      {
        denominationKey: 500,
        denominationValue: +this.denominationForm.get('five_hundred')?.value
      },
      {
        denominationKey: 200,
        denominationValue: +this.denominationForm.get('two_hundred')?.value
      },
      {
        denominationKey: 100,
        denominationValue: +this.denominationForm.get('one_hundred')?.value
      },
      {
        denominationKey: 50,
        denominationValue: +this.denominationForm.get('fifty')?.value
      },
      {
        denominationKey: 20,
        denominationValue: +this.denominationForm.get('twenty')?.value
      },
      {
        denominationKey: 10,
        denominationValue: +this.denominationForm.get('ten')?.value
      },
      {
        denominationKey: 1,
        denominationValue: +this.denominationForm.get('one')?.value
      }
    ];

    let denomPayload = {
      title: this.unique_key,
      total: this.total,
      denomination: final,
      data: this.denominationForm.value
    };

    localStorage.setItem(this.unique_key, JSON.stringify(denomPayload));
  }

  ngOnDestroy(): void {
    this.saveDenom();
  }

}
