import { Injectable } from '@angular/core';
import {
  HttpClient,
  HttpErrorResponse,
  HttpHeaders,
  HttpParams,
} from '@angular/common/http';
import { ShowMessageService } from './showMessage/show-message.service';
import { BehaviorSubject, Observable, Subject, of, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { People } from '../pages/v2/loan-disbursement-v2/loan-disbursement-v2.page';
import { staticData } from '../config/db.constant';

export const MICROSERVICE_URL = environment.microServiceURL;
export const DB_Data = staticData;
@Injectable({
  providedIn: 'root',
})
export class ApiServiceV2 {
  constructor(
    private http: HttpClient,
    private showMessageService: ShowMessageService
  ) {}

  fetchCustomerByMobile(mobNo) {
    return this.http.get<any>(
      `${MICROSERVICE_URL}/customer?phoneNumber=${mobNo}`
    );
  }

  public relationshipType(): Observable<string[]> {
    return of(DB_Data.relationshipType);
  }

  getCustomerInfo(cifNumber: any) {
    return this.http.get<any>(
      `${MICROSERVICE_URL}/customer-api?cifNumber=${cifNumber}`
    );
  }

  getProfileDetails(customerId: any) {
    return this.http.get<any>(
      `${MICROSERVICE_URL}/customer-api?customerId=${customerId}`
    );
  }
}
