import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { of as observableOf, Observable } from "rxjs";
import { map } from "rxjs/operators";
import { CountryData } from "../data/country";
import { environment } from "src/environments/environment";

const MICROSERVICE_URL = environment.microServiceURL;
const staticUrl = "http://192.168.0.127:8765";

@Injectable({
  providedIn: "root",
})
export class CountryService extends CountryData {
  countries: any;

  constructor(private http: HttpClient) {
    super();
  }

  loadCountry(): Observable<any> {
    if (this.countries) {
      return observableOf(this.countries);
    } else {
      return this.getCountries();
    }
  }

  private getCountries(): Observable<any> {
    return this.http
      .get(`${staticUrl}/country`)
      .pipe(map(this.processData, this));
  }

  private processData(data: any) {
    this.countries = data;
    return this.countries;
  }
}
