import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
export const API_URL = environment.BASE_URL;

const MICROSERVICE_URL = environment.microServiceURL;

@Injectable({
  providedIn: 'root',
})
export class BranchService {
  constructor(private httpClient: HttpClient) {}

  // SIMULATE
  uploadAndProgress(file: File) {
    console.log(file);
    var formData = new FormData();
    formData.append('file', file);
    return this.httpClient.post('https://file.io', formData, {
      reportProgress: true,
      observe: 'events',
    });
  }

  // fetchCustomerDetails(payload: any) {
  //   return this.httpClient.post<any>(
  //     `${API_URL}/customerdata/phoneNoEmailIdExists`,
  //     payload
  //   );
  // }
  fetchCustomerDetails(payload: any) {
    return this.httpClient.post<any>(
      `${MICROSERVICE_URL}/customer/phone-no-emailid-exists`,
      payload
    );
  }

  // upsertCustomer(data: object) {
  //   return this.httpClient.post<any>(
  //     `${API_URL}/customerdata/upsertCustomerDetails`,
  //     data
  //   );
  // }
  upsertCustomer(data: object) {
    return this.httpClient.post<any>(`${MICROSERVICE_URL}/kyc-info`, data);
  }

  // updateKyc(kycDetails: any) {
  //   return this.httpClient.post<any>(
  //     `${API_URL}/customerdata/saveRekycOrAcceptInfo`,
  //     kycDetails
  //   );
  // }
  updateKyc(kycDetails: any) {
    return this.httpClient.post<any>(
      `${MICROSERVICE_URL}/customer/saveRekycInfo`,
      kycDetails
    );
  }

  getOtp(payload) {
    return this.httpClient.get<any>(
      `${MICROSERVICE_URL}/auth/generateOTP`,
      payload
    );
  }

  verifyOtp(obj: object) {
    return this.httpClient.post<any>(`${MICROSERVICE_URL}/auth/verifyOTP`, obj);
  }
  // upload(uploadData) {
  //   return this.httpClient.post<any>(
  //     `${API_URL}/rest/upload/uploadDoc`,
  //     uploadData,
  //     {
  //       reportProgress: true,
  //       observe: 'events',
  //     }
  //   );
  // }
  upload(uploadData) {
    return this.httpClient.post<any>(
      `${MICROSERVICE_URL}/upload-document`,
      uploadData
    );
  }
  // for saving document
  UploadDocument(data?) {
    return this.httpClient.post<any>(`${MICROSERVICE_URL}/documents`, data);
  }

  deleteCustDoc(customerId, documentName, documentSide) {
    return this.httpClient.delete<any>(
      `${API_URL}/customerdata/deleteCustDocument?customerId=${customerId}&documentName=${documentName}&documentSide=${documentSide}`
    );
  }

  saveOrUpdateBio(formData) {
    return this.httpClient.post(
      `${API_URL}/rest/upload/saveOrUpdateBio`,
      formData
    );
  }

  // updateKycStatusByCustId(customerId, kycStatus, custStatus) {
  //   console.log(customerId);
  //   var data = {
  //     customerId: +customerId,
  //     kycStatus: kycStatus,
  //     custStatus: custStatus,
  //   };
  //   return this.httpClient.put<any>(
  //     `${API_URL}/customerdata/updateKycStatusByCustomerId`,
  //     data
  //   );
  // }

  uploadBiometric(formData) {
    return this.httpClient.post(
      `${MICROSERVICE_URL}/verification/uploadBiometricDoc`,
      formData
    );
  }

  fetchStateCityByZipcode(countryName, pincode) {
    return this.httpClient.get(
      `${API_URL}/iccity/fetchCityByCountryAndPincode?countryName=${countryName}&pinCode=${pincode}`
    );
  }
  fetchCustomerDocuments(customerId, documentType) {
    return this.httpClient.get(
      `${API_URL}/customerdata/fetchCustomerDocuments?customerId=${customerId}&documentType=${documentType}`
    );
  }
}
