import { DatePipe } from '@angular/common';
import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatCalendarCellClassFunction, MatCalendarCellCssClasses } from '@angular/material/datepicker';
import { ModalController } from '@ionic/angular';
import { MomentDateAdapter } from '@angular/material-moment-adapter';

class CustomDateAdapter extends MomentDateAdapter {

  getDayOfWeekNames(style: 'long' | 'short' | 'narrow') {
    return ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
  }
}

@Component({
  selector: 'app-transaction-date',
  templateUrl: './transaction-date.component.html',
  styleUrls: ['./transaction-date.component.scss'],
  providers: [
    {
      provide: DateAdapter,
      useClass: CustomDateAdapter,
      deps: [MAT_DATE_LOCALE],
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: {
        parse: {
          dateInput: ['l', 'LL'],
        },
        display: {
          dateInput: 'L',
          monthYearLabel: 'MMMM YYYY',
          dateA11yLabel: 'L',
          monthYearA11yLabel: 'MMMM YYYY',
        },
      },
    },
  ],
})
export class TransactionDateComponent implements OnInit {
  selected: any;
  @Input() date: Date;
  @Input() screenType: any
  datesToBeHighlighted;
  minDate = new Date();
  maxDate = new Date(2050, 11, 31);

  minDateDOB = new Date(1900, 11, 31);
  maxDateDOB = new Date()

  constructor(private modalCtr: ModalController,
    private datepipe: DatePipe) { }

  ngOnInit() {
    console.log(this.date);
    this.selected = this.date ? this.date : new Date();
    console.log(this.selected);
  }

  disableWeekendsFilter = (d: Date): boolean => {
    let day = new Date(d);
    return (day.getDay() !== 0 && day.getDay() !== 6);
  }

  // disableFuture() {
  //   return (date: Date): MatCalendarCellCssClasses => {
  //     let day = new Date(date);
  //     console.log(day)
  //     console.log(day.getDay());
  //     const d = new Date();
  //     let year = d.getFullYear();
  //     return ((day.getDay() == 0 || day.getDay() == 6) && Number(day.getFullYear()) == Number(year) ? 'special-date' : '');
  //     // return (day.getDay() !== 0 && day.getDay() !== 6 ? '' : 'special-date');
  //   };
  // }
  dateClass: MatCalendarCellClassFunction<Date> = (date, view) => {
    const cellDate = new Date(date);
    if (view === "month") {
      const highlightDate = cellDate.getDay() == 0 || cellDate.getDay() == 6
      return highlightDate ? "special-date" : "";
    }
    return "";
  };


  close() {
    this.modalCtr.dismiss();
  }

  done() {
    const convertedDate = this.datepipe.transform(this.selected, 'yyyy-MM-dd');
    this.modalCtr.dismiss(convertedDate);
  }


}
