import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FingerprintScanComponent } from '../../fingerprint-scan/fingerprint-scan.component';
import { ModalController } from '@ionic/angular';
import { CaptureImageComponent } from '../../capture-image/capture-image.component';
// import {TripOriginIcon} from '@mui/icons-material/TripOrigin'

@Component({
  selector: 'app-customer-auto-verification',
  templateUrl: './customer-auto-verification.component.html',
  styleUrls: ['./customer-auto-verification.component.scss'],
})
export class CustomerAutoVerificationComponent implements OnInit {
  documentNumber: any;
  documentName: any;
  scannedItem: string;
  otpValue: any;
  @Output() enableSubmit = new EventEmitter();

  isOtp: boolean;
  isBiometric: boolean;
  irisData = {
    title: 'Scan Irish',
    desc: 'Place the Irish and scan to proceed',
  };

  faceData = {
    title: 'Scan Face ID',
    desc: 'Place the face and scan to proceed',
  };

  front_data = {
    title: 'frontScan',
    desc: 'front scanned',
  };

  back_data = {
    title: 'Backscan',
    desc: 'back scanned',
  };

  fingerPrintData = {
    title: 'Scan fingerprint',
    desc: 'place the finger and scan to proceed',
  };

  docArr: any[] = ['Aadhar Card', 'Pan Card', 'Passport'];

  constructor(private modalController: ModalController) {}

  ngOnInit() {}

  verificationMethodToggle(evt) {
    console.log(evt);
    if (!this.documentName || !this.documentNumber) {
      return;
    }

    if (evt == 'otp') {
      this.isOtp = true;
      this.isBiometric = false;
    } else {
      this.isOtp = false;
      this.isBiometric = true;
    }
  }

  async openFingerPrint(data) {
    let modal = await this.modalController.create({
      component: FingerprintScanComponent,
      backdropDismiss: true,
      componentProps: {
        screenData: data,
      },
    });
    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
    });

    return await modal.present();
  }

  async openScanner(data) {
    if (data == 'irisData') {
      this.scannedItem = 'iris';
    } else {
      this.scannedItem = 'face';
    }
    let modal = await this.modalController.create({
      component: CaptureImageComponent,
      backdropDismiss: true,
      componentProps: {
        scanObject: this.scannedItem,
      },
    });
    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
    });

    return await modal.present();
  }

  valid(value) {
    if (this.otpValue.toString().length == 6) {
      console.log('calling');

      this.enableSubmit.emit(true);
    }
  }
}
