import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TransactionDateComponent } from '../transaction-date/transaction-date.component';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-professional-information',
  templateUrl: './professional-information.component.html',
  styleUrls: ['./professional-information.component.scss'],
})

export class ProfessionalInformationComponent implements OnInit {

  constructor(private fb:FormBuilder,private modalCtrl:ModalController) { }

  ngOnInit() {
    this.createForm();
  }
  professionalInfoForm:FormGroup
  employeeTypeArr: string[];
  createForm(){
    this.professionalInfoForm = this.fb.group({
    offAddresses: this.fb.array([this.addAddressForm()]),
    });
  }
  addAddressForm(){
    return this.fb.group({
      employeeType: ['', [Validators.required]],
      employeeName:['', [Validators.required]],
      designation:['',[Validators.required]],
      empStDt: ['', [Validators.required]],
      empEdDt: ['', [Validators.required]],
      officialPh:['',[Validators.required]],
      officialMail:['',[Validators.required]],
      address1: ['', [Validators.required]],
      address2: ['',[Validators.required]],
      country: ['', [Validators.required]],
      zipCode: ['', [Validators.required]],
      state: ['', [Validators.required]],
      city: ['', [Validators.required]],
      empMail: ['', [Validators.required]],
      empNumber: ['', [Validators.required]],
      empPhone: ['', [Validators.required]],
      empFax :['', [Validators.required]],
    });

  }
  
  datePopup() {
    this.modalCtrl
      .create({
        component: TransactionDateComponent,
        componentProps: {
          screenType: 'basicInfo',
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          console.log(res, 'after dismiss');
          // this.professionalInfoForm.get('dateOfBirth').setValue(res?.data);
        });
      });
  }
 get offAddresses():FormArray{
    return this.professionalInfoForm.get('offAddresses') as FormArray;
  }
  addAnotherDetails(){
    this.offAddresses.push(this.addAddressForm());

  }
  removeOfficialDetails(index:number){
    this.offAddresses.removeAt(index);
  }
}
