import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { of as observableOf, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { CityData } from '../data/city';
import { environment } from 'src/environments/environment';

const MICROSERVICE_URL = environment.microServiceURL;
const staticUrl = 'http://192.168.0.127:8765';
@Injectable({
  providedIn: 'root',
})
export class CityService extends CityData {
  cities: any;

  constructor(private http: HttpClient) {
    super();
  }

  loadCity(): Observable<any> {
    if (this.cities) {
      return observableOf(this.cities);
    } else {
      return this.getCities();
    }
  }

  private getCities(): Observable<any> {
    return this.http.get(`${staticUrl}/city`).pipe(map(this.processData, this));
  }

  private processData(data: any) {
    this.cities = data;
    return this.cities;
  }
}
